#resume{
    .loading-bg{
        background: white;
        position: fixed;
        top: 0;
        left: 0;
        height: 100vh;
        width: 100vw;
        transform: translate(0, 100%); 
        z-index: 100;
    }
    .info-section{
        width: 20%;
    }

    .content-area{
        margin-left: 20%;
        width: 80%;
        background: white; //$color3;
        min-width: 300px;
        height: 100%; 
        overflow: hidden;
        background: white; //$color3;
    }

    .category{
        margin-bottom: 56px;
        
        h1{
            font-weight: 700;
            font-size: 2rem;
            text-transform: uppercase;
            margin-bottom: 56px;
        }
    
        .xp-section{
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-column-gap: 56px;
            grid-row-gap: 30px;
            max-width: 1000px;
    
            .xp-box{
                padding: 30px;
                transition: all 0.5s ease-in-out;
                &:hover{
                    -webkit-box-shadow: 0px 20px 74px 6px rgba(0,0,0,0.12);
                    -moz-box-shadow: 0px 20px 74px 6px rgba(0,0,0,0.12);
                    box-shadow: 0px 20px 74px 6px rgba(0,0,0,0.12);
                }
                
    
                h5{
                    font-weight: 600;
                    font-size: 1.3rem;
                    margin-bottom: 1.4rem;
                }
    
                p{
                    font-weight: 300;
                    font-size: 1rem; 
                    margin-bottom: 1rem;
                }
            }
        }
    }
}

//phone
@media (min-width: 0px) and (max-width: 767px){
    html, body{
        height: auto;
    }

    #resume{
        .menu-icon{
            top: -3px;
            right: 20px;
            color: white;

            .fa{
                color: white;
            }
        }

        .info-section{
            padding: 10px 30px;
            position: static;
            border: 3px solid red;
            .logo{
                height: 50px;
                width: 70px;
                font-size: 1rem;
            }

            .info-box{
                display: none;
            }
        }

        .content-area{
            border: 3px solid black;
            // margin-left: 0;
            margin-right: 30%;
            width: 100%;
            height: auto !important;


            &:after{
                content: "";
                display: table;
                clear: both;
            }
            .scroll-section{
                padding: 50px 20px;
                width: 100%;
                height: auto;
                overflow: auto;
            }
            .category{
                width: 100%;
                .xp-section{
                    grid-template-columns: 1fr;

                }
            }

        }
    }
}
//tablet
@media (min-width: 768px) and (max-width:991px){
    #resume{
        .info-section{
            width: 300px;
            padding: 0;

            .logo{
                margin: 0 auto;
                margin-top: 50px;
            }
            .info-box{
                width: 100%;
                padding: 56px 10px;
                margin-bottom: 40px;
                text-align: center;
            }
            .icon-section {
                a{
                    font-size: 1.4rem;
                }
            }
        }
        .content-area{
            height: auto;
            width: 70%;
            margin-left: 30%;

            &:after{
                content: ""; 
                display: table;
                clear: both;
            }
            
            .category{
                width: 100%;
                .xp-section{
                    grid-template-columns: 1fr;

                }
            }
        }
    }
}
//laptops
@media (min-width: 992px) and (max-width: 1199px){
    #resume{
        .info-section{
            width: 300px;
        }
        .icon-section {
            a{
                font-size: 1.4rem;
            }
        }
    }
    .content-area{
        height: auto;
        width: 70%;
        margin-left: 30%;

        &:after{
            content: ""; 
            display: table;
            clear: both;
        }
    }
}
//extra big
@media (min-width: 1200px){
    #resume{
        .info-section{
            width: 30%;
        
            .icon-section{
                a{
                    font-size: 1.4rem;
                }
            }
        }
    }
}

@media (max-width: 1600px){


    #resume{
        .info-section{
            width: 30%;
        }
        .content-area{
            width: 70%;
            margin-left: 30%;

        }
    }
    
}