                }
            }
        }
        .content-area{
            height: auto;
            &:after{
                content: ""; 
                display: table;
                clear: both;
            }

            .img-container{
                width: 100%;
                padding: 56px 20px 0;
                height: auto;

                .form-section{
                    position: static;
                    width: 100%;
                    height: auto;
                    overflow: auto;
                }
            }
            .contact-section{
