}
//laptops
@media (min-width: 992px) and (max-width: 1199px){
    #contact{
        .content-area{
            height: auto;

            &:after{
                content: "";
                display: table;
                clear: both;
            }
            .img-container{
                padding: 56px 20px 0;
                float: left;
                height: auto;
                width: 100%;

                .form-section{
                    position: static;
                    width: 100%;
                    float: left;
                    height: auto;
                    overflow: auto;
                }
            }

            .contact-section{
                width: 70%;
                float: left;
                padding: 20% 20px 0;
                max-width: 800px;

                
            }
        }
    }
}
