            .img-container{
                padding: 56px 20px 0;
                float: left;

                .form-section{
                    left: -157px;
                    width: 400;
                }
            }

            .contact-section{
                width: 70%;
                float: left;
                padding: 20% 60px 0;
                max-width: 800px;

            }
        }
    }
}

